.post-wrapper {
	width: 100%;
}

.has-sidebar:not(.home) {

	.hentry {
		max-width: 1000px;

		@media only screen and (min-width: 900px) {
			display: flex;
			flex-direction: row;
		}

		.widget-area {

			@media only screen and (max-width: 899px) {
				margin-top: 3em;
			}

			@media only screen and (min-width: 900px) {
				flex: 0 0 19em;
				padding-left: 4.5em;
				padding-top: .55em;
			}
		}
	}
}

.widget-area--sidebar {

	@media only screen and (max-width: 599px) {
		padding-top: 4.2em;
	}

	@media only screen and (max-width: 899px) {
		border-top: 1px solid #dfdfdf;
		padding-top: 5em;
	}

	body.single-post & {
		@media only screen and (max-width: 899px) {
			padding-top: 3em;
		}
	}

	.h4 {
		margin-bottom: .25em;
	}

	.widget {
		margin-bottom: 2.5em;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	.widget_text p:last-of-type {
		margin-bottom: 0;
	}

	.search-form label {
		display: none;
	}

	.search-form .search-field {
		margin-bottom: 5px;
	}

	.widget > ul {
		margin-bottom: 5px;
	}
}
