.wp-block-cover-image {
	margin-top: 2.2em;
	margin-bottom: 2.1em;

	@media only screen and (min-width: 600px) {
		margin-top: 2.5em;
		margin-bottom: 2.37em;
	}

	&.alignfull {
		width: 100vw;
	}

	&-text {
		@include fluid-type(514px, 820px, 24px, 32px);
		margin: 0;
		padding: 0;
		font-family: $font__heading;
		font-weight: 800;

		.alignfull & {
			@include fluid-type(514px, 820px, 28px, 40px);
		}
	}
}
