.size-full,
.size-large,
.alignnone:not(.wp-block-gallery) {
	display: block;
	height: auto;
	max-width: 100%;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Gutenberg embeds and FitVids videos */
.wp-block-embed,
.fluid-width-video-wrapper {
	margin: 2.2em 0 2.1em;

	@media (min-width: 600px) {
		margin: 2.4em 0 2.3em;
	}
}

.wp-block-embed .fluid-width-video-wrapper {
	margin: 0;
}

/* Chrome fix for emoji image replacements */
.entry-content {

	img.emoji,
	.img.wp-smiley {
		opacity: 1;
		transition: none;
	}
}

.wp-block-image {
	margin: 0 0 1.5em;

	@media (min-width: 600px) {
		margin: 0 0 1.85em;
	}
}

