#amazon-polly-by-tab {
	@include hide();
}

#amazon-polly-audio-tab {
	padding: 0;
}

#amazon-polly-audio-table {
	margin: 0 0 2.25em;
	padding-bottom: 2.6em;
	border-bottom: 1px solid rgba(0, 0, 0, .1) !important;

	@media (min-width: 600px) {
		margin: 0 0 2.7em;
		padding-bottom: 3em;
	}

	body:not(.single) & {
		display: none;
	}

	.has-post-thumbnail & {
		margin: 0 0 2.1em;
		padding-bottom: 2.35em;

		@media (min-width: 600px) {
			margin: 0 0 2.55em;
			padding-bottom: 2.9em;
		}
	}
}

.amazon-polly-label {
	margin-bottom: .6em;

	body:not(.single) & {
		display: none;
	}

	.icon {
		height: 28px;
		width: 28px;
		position: relative;
		left: -4px;
		fill: $gray;
		opacity: .8;
	}

	&-text {
		margin-bottom: 0;
		top: 1px;
	}
}