.home:not(.blog):not(.no-home-styles):not(.has-tabor-blocks) {

	.page {
		padding-top: 3.25em;

		@media only screen and (min-width: 700px) {
			padding-top: 4.5em;
		}
	}

	.site-footer {
		padding-top: 5.5em;

		@media only screen and (min-width: 600px) {
			padding-top: 9em;
		}
	}

	.entry-content {
		position: relative;

		p:first-of-type {
			margin-top: 2em;

			@media only screen and (min-width: 600px) {
				margin-top: 9em;
			}
		}

		h5 {
			color: $gray;
			margin-top: 0;
			padding-top: 0;
			margin-bottom: 2em;
			margin-top: -1em;
			font-weight: 500;
		}

		h1 {
			@include fluid-type(600px, 1200px, 90px, 150px);
			line-height: 1.4;
			margin-bottom: 0;
			padding: 0;
			white-space: nowrap;

			@media only screen and (max-width: 599px) {
				@include font-size(70px);
			}

			@media only screen and (max-width: 399px) {
				@include font-size(52px);
			}

			& + h1 {
				display: block;
				margin-bottom: 0;
				margin-left: 3vw;
				line-height: 1.2;

				@media only screen and (min-width: 400px) {
					margin-left: 5.5vw;
				}
			}

			& + .typed-content {
				margin-left: 3.2vw;

				@media only screen and (min-width: 400px) {
					margin-left: 5.7vw;
				}

				h2 {
					padding: 0 !important;
					margin: 0 !important;
				}
			}
		}
	}
}
