h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	color: $black;
	font-family: $font__heading;
	font-weight: 800;
	margin: 0;

	.entry-content &,
	.comment-content & {
		margin: -0.35em 0 0.25em;
		padding-top: 1em;

		a {
			box-shadow: none !important;
		}
	}

	.entry-content & + & {
		padding-top: 0.25em;
	}
}

h1:not(.site-title),
.h1:not(.site-title) {
	@include fluid-type(514px, 1200px, 32px, 55px);
	@include fluid-line-height(514px, 1200px, 1.3, calc(1.25em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.2);

	.font-size--large & {
		@include fluid-type(514px, 1200px, 33px, 57px);
	}

	.font-size--larger & {
		@include fluid-type(514px, 1200px, 34px, 58px);
	}

	.font-size--largest & {
		@include fluid-type(514px, 1200px, 35px, 59px);
	}
}

h2,
.h2 {
	@include fluid-type(514px, 820px, 28px, 34px);
	@include fluid-line-height(514px, 1200px, 1.3, calc(1.25em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.25);

	.font-size--large & {
		@include fluid-type(514px, 820px, 28px, 35px);
	}

	.font-size--larger & {
		@include fluid-type(514px, 820px, 28px, 36px);
	}

	.font-size--largest & {
		@include fluid-type(514px, 820px, 28px, 37px);
	}

	.entry-content & {
		@include fluid-type(514px, 820px, 24px, 30px);
		@include fluid-line-height(514px, 1200px, 1.3, calc(1.25em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.2);
	}
}

h3,
.h3 {
	@include fluid-type(514px, 820px, 24px, 28px);
	@include fluid-line-height(514px, 1200px, 1.3, calc(1.25em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.2);

	.entry-content & {
		@include fluid-type(514px, 820px, 24px, 27px);
		@include fluid-line-height(514px, 1200px, 1.3, calc(1.25em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.2);
	}
}

h4,
.h4 {
	@include fluid-type(514px, 820px, 20px, 25px);
	@include fluid-line-height(514px, 1200px, 1.6, calc(1.2em + (1.5 - 1.3) * ((100vw - 21em)/(35 - 21))), 1.5);
}

h5,
.h5 {
	@include font-size(16px);
	line-height: 1.3;

	@media only screen and (min-width: 600px) {
		@include font-size(18px);
	}

	.font-size--larger & {
		@include font-size(19px);
	}
}

h6,
.h6 {
	@include font-size(14px);

	@media only screen and (min-width: 600px) {
		@include font-size(16px);
	}
}
