.bctt-click-to-tweet {
	@extend blockquote;
	display: block;

	.icon {
		fill: $twitter;
	}

	.bctt-ctt-btn  {
		color: $twitter;
	}

	&:hover {

		.icon {
			fill: $twitter;
		}

		.bctt-ctt-btn  {
			color: $twitter;
			box-shadow: inset 0 0 0 currentcolor, 0 2px 0 currentcolor !important;
		}
	}

	.bctt-ctt-text a {
		box-shadow: none !important;
		display: block;
	}

	.bctt-ctt-btn {
		@include font-size(18px);
		display: inline-block;
		margin-top: -6px;
		margin-left: 1px;
		cursor: pointer;
		transition: color .2s cubic-bezier(.694,.0482,.335,1);
		box-shadow: none !important;
	}

	.icon {
		fill: currentColor;
		width: 40px;
		top: 14px;
		margin-right: -10px;
		position: relative;
		height: 38px;
		margin-left: -3px;
		transition: fill .2s cubic-bezier(.694,.0482,.335,1);

		@media only screen and (min-width: 600px) {
			top: 13px;
		}
	}
}