// Gutenberg galleries
ul.wp-block-gallery {
	margin-top: 2.2em;
	margin-bottom: 2.1em;
	padding: 0;
	margin-left: -8px;

	@media only screen and (min-width: 600px) {
		margin-left: -8px;
		margin-top: 1.9em;
		margin-bottom: 1.9em;
	}

	& + & {
		margin-top: -1.9em;
	}

	li.blocks-gallery-item {
		padding-left: 0;
		display: flex;
	}

	&.alignwide {
		margin-left: -8px;
		width: calc(100% + 16px);

		@media only screen and (min-width: 600px) {
			margin-left: calc(-4vw - 8px);
		}

		@media only screen and (min-width: 870px) {
			margin-left: calc(-7vw - 8px);
		}
	}

	&.alignfull {
		margin-left: calc(-100vw / 2 + 100% / 2) !important;
		margin-right: calc(-100vw / 2 + 100% / 2) !important;
	}
}

.wp-block-gallery {

	&.columns-1 .blocks-gallery-item {

		figcaption {
			background: $white;
		}

		&:not(:last-child) {

			figcaption {
				padding-top: 0.75em;
				padding-bottom: 1.5rem;
			}
		}

		&:last-child {

			figcaption {
				padding-bottom: 0;
				margin-bottom: -0.25rem;
			}
		}
	}

	.blocks-gallery-item {

		figcaption {
			background: rgba($white, 0.7);
			padding-top: 1.2em;
			padding-bottom: 1.2em;
			bottom: 0;
			margin-bottom: 0;
		}

		// Position captions for galleries with one image (which is common).
		&:nth-child(1):last-child {

			figcaption {
				background: $white;
				bottom: -34px;
				margin-bottom: 1.7rem;

				@media (min-width: 600px) {
					margin-bottom: 1.3rem;
				}
			}
		}
	}
}

// Wide Alignment galleries
.alignfull {
	margin-left: calc(-100vw / 2 + 100% / 2) !important;
	margin-right: calc(-100vw / 2 + 100% / 2) !important;
	max-width: 100vw;
	width: 100vw;
	margin-top: 2.3em;
	margin-bottom: 2.3em;

	@media only screen and (min-width: 600px) {
		margin-top: 2.8em;
		margin-bottom: 2.44em;
	}

	img {
		width: 100%;
	}
}

.alignwide {
	margin: 2.2em 0 2.1em;

	@media only screen and (min-width: 600px) {
		margin: 2.5em 0 2.37em;
		margin-left: -4vw !important;
		max-width: calc(100% + 8vw) !important;
		width: calc(100% + 8vw) !important;
	}

	@media only screen and (min-width: 870px) {
		margin-left: -7vw !important;
		width: calc(100% + 14vw) !important;
		max-width: calc(100% + 14vw) !important;
	}
}

.aligncenter.wp-block-image {
	margin: 2.2em auto 2.1em;

	@media only screen and (min-width: 600px) {
		margin-top: 2.5em;
		margin-bottom: 2.37em;
	}
}

.wp-block-image.alignleft,
.wp-block-image.alignright {

	img {
		width: inherit;
	}
}
